import React from 'react';
import { connect } from 'react-redux';
import {addToCart,deleteFromCart} from "../actions";
import Nutrition from "./Nutrition";

class AddToCart extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            size : "Regular"
        };
        this.changeSize = this.changeSize.bind(this);
    }
    changeSize(e){
        this.setState({
            size: e.target.value
        });
    }
    render(){
        const count = () => {
            for(let i=0; i<this.props.cart.length;i++){
                if(this.props.cart[i].product_id===this.props.product&&this.props.cart[i].size===this.state.size){
                    return this.props.cart[i].quantity;
                }
            }
        }
        const quantity = count();
        return (
            <div>
                <span className="product-count" style={{opacity:quantity?1:0, transform: quantity?"scale(1)":"scale(0)"}}>{quantity}</span>
                <Nutrition display={this.props.nutrition} nutritional={this.props.sizes.filter(size=>size.size===this.state.size)[0].nutritional}/>
                <p>{this.props.name}</p>
                <div className="select">
                    <select onChange={this.changeSize}>
                        {this.props.sizes.map(size=>{
                            return (
                                <option key={size.size} value={size.size}>{size.size}: ${size.price}</option>
                            );
                        })}
                    </select>
                </div>
                <i className="fa fa-minus" onClick={()=>{if(quantity){return this.props.dispatch(deleteFromCart(this.props.product,this.state.size))}}}></i>
                <i className="fa fa-plus" onClick={()=>{this.props.dispatch(addToCart(this.props.product,this.state.size))}}></i>
            </div>
        );
    }
}

export default connect(state=>({
    cart: state.cart
}))(AddToCart);


